.board-ops {
    color : #323338;
    margin: 2px 0 15px 0;

    .left-ctrl {
        position: sticky;
        left    : 130px;
        z-index : 5;
        &>*:not(:last-child) {
            margin-inline-end: 10px;
        }
    }

    button {
        font-family: inherit;
        color      : inherit;
    }


    .btn-add-group,
    .view,
    .btn-filter,
    .btn-sort,
    .btn-search {
        font-family  : roboto-light, helvetica, sans-serif;
        height       : 32px;
        font-size    : rem(14px);
        padding      : 4px 8px;
        font-weight  : 400;
        line-height  : 22px;
        border       : none;
        border-radius: 4px;
    }

    .btn-add-group {
        background-color: $Darkblue;
        color           : white;
        width           : 90px;

        &:hover {
            background-color: #0556ad;
        }
    }

    .btn-filter,
    .view,
    .btn-sort,
    .btn-search {
        background-color: transparent;
        transition      : 0.3s;

        &:hover {
            background-color: #e6e9ef;
        }

        &>*:not(:last-child) {
            margin-inline-end: 10px;
        }
    }

    .btn-search svg,
    .board-search svg {
        width : 20px;
        height: 20px;
    }

    .btn-filter>*:not(:last-child) {
        margin-inline-end: 5px;
    }

    .view>* {
        margin-inline-end: 5px;
    }

    svg {
        width : 16px;
        height: 16px;
    }

    .fa-board-search {
        font-family: fa-solid;
    }

    .fa-board-search::before {
        content: '\f002';
    }

    .active {
        border-bottom: 2px solid #0085ff;
    }

    .right-ctrl {
        font-family: roboto-light, helvetica, sans-sarif;
        position   : sticky;
        right      : 25px;
    }

    .right-ctrl>* {
        border-bottom-left-radius : unset;
        border-bottom-right-radius: unset;

        svg {
            opacity: 0.75;
        }
    }

    .view {
        cursor: pointer;
    }
}

@media (max-width: $mobile-breakpoint) {
    .board-ops {
        margin-top: 140px;
        width     : 100%;
        display   : block;
        position  : sticky;
        left      : 0px;
        z-index   : 6;

        .btn-add-group {
            min-width   : 100px;
            overflow: hidden;
            // text-overflow: ellipsis;
        }

        .left-ctrl {
            position: unset;
            gap     : 10px;
        }

        .right-ctrl {
            margin-top: 5px;
            position  : unset;

        }
    }
}