.board-filter {
    cursor          : pointer;
    animation       : fade-in 0.3s ease-in-out both;
    position        : absolute;
    top             : 40px;
    left            : 175px;
    padding         : 10px;
    background-color: white;
    box-shadow      : 0 8px 16px 0px #00000052;
    z-index         : 1;
    font-size       : rem(13px);
    border-radius   : 4px;
    max-height      : 150px;
    overflow-y      : scroll;
    z-index         : 5;

    h1 {
        font-size: rem(16px);
    }

    .filter-groups {
      
        .filter-group {
            h1 {
                font-size: rem(13px);
            }

            &.status{
                width: 110px;
            }
            &.tag{
                width: 115px;
            }
            &.priority{
                width: 80px;
            }
            &.member{
                width: 60px;
            }
        }
    }

    .filter-groups>*:not(:last-child) {
        margin-inline-end: 15px;
    }
}

@media (max-width: $mobile-breakpoint) {
    .board-filter {
        left: 50px;
        width: 75%;
        overflow-x: scroll;
        .filter-groups {
            flex-wrap: wrap;
        }
    }
}